@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.sidebar-container {
  position: relative;
  width: 20vw;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: #0e419d;
}

.sidebar {
  position: sticky;
  height: 100vh;
  width: 20vw;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: #0e419d;
  display: flex; /* Add flex display to center the footer */
  flex-direction: column; /* Stack the children vertically */
}

.sidebar-footer {
  position: relative;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: medium;
  padding: 40px;
  text-align: center;
  top: 140px;
}

.sidebar-footer a {
  text-decoration: none;
  color:#ffff
