@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.weighbridge {
  position: relative;
  width: calc(100% - 18vw);
  .Cbody {
    width: 95%;
    margin-top: 1vw;
  }
  .MuiPaper-root.MuiPaper-elevation.MuiPaper-rounded.MuiPaper-elevation3.css-3su884-MuiPaper-root {
    padding: 1vw;
    background-color: #d2e3fc

  }

  .time-control {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 0.9rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }

  &__container {
    @include flexColumnACenter;
    background: $bg_light_grey;
    height: 100%;
    overflow-y: scroll;
  }
  &__header {
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 999;
    padding: $font_xsmall $font_xsmall;
    @include flexColumnACenter;
    background: $primary_white;
    margin-bottom: 1vw;
    &__section {
      width: 100%;
      display: flex;
      justify-content: space-between;
      &__main {
        padding: 0 0 0.3vw 0;
        @include flexbetween;
        flex-direction: column;
        h4 {
          font-size: $font_button;
        }
        h5 {
          font-size: $font_verysmall;
          color: $font_grey;
          font-weight: 400;
        }
      }
      &__bottom {
        h4 {
          font-size: $font_button;
        }
      }
    }
    border-bottom: 1px solid #0e419d;
  }
  &__table {
    padding: $font_regular 0;
    width: 98%;
    
    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 25%;
        }
        button {
          width: auto;
          border: 1px solid grey;
          background: #0e419d;
          padding: 0.6vw 2vw;
          border-radius: $font_xsmall;
          color: $primary_white;
        }
      }
    }
   
    .striped-table tbody tr td,
    th {
      background-color: transparent;
      font-size: $font_xsmall !important;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #ffff;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #d2e3fc;
    }
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background: #8ab4f8;
    }
  }
  .col-lg-4 {
    margin-bottom: 0.5vw !important ;
  }

  .form-label {
    font-size: 0.9vw;
    margin-bottom: 0 !important;
  }
  &__popup {
    position: absolute;
    top: 5.9vw;
    width: 100%;
    @include flexJCenter;
    background: $bg_light_grey;
    &__container {
      width: 90%;
    }
  }
}

.download {
  width: 2vw;
  height: 2vw;  
  position: absolute;
  right: 10px;
  top: 1vw;
}
.empty_data {
  height: -webkit-fill-available;
  display: flex;
  align-items: center;
  justify-content: center;
  h1 {
    text-align: center;
  }
  h3 {
    text-align: center;
  }
}
.back {
  position: absolute;
  img {
    width: 2vw;
    height: 2vw;
  }
}
