@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.procurement {
  position: relative;
  width: calc(100% - 18vw);
  &__container {
    @include flexColumnACenter;
    background: $bg_light_grey;
    height: 100%;
  }
  &__header {
    width: 100%;
    padding: $font_xsmall $font_xsmall;
    @include flexColumnACenter;
    background: $primary_white;
    &__section {
      width: 100%;
      &__main {
        padding: 0 0 $font_xverysmall 0;
        @include flexbetween;
        h5 {
          font-size: $font_xsmall;
          color: $font_grey;
          font-weight: 400;
        }
      }
      &__bottom {
        h4 {
          font-size: $font_large;
        }
      }
    }
    border-bottom: 1px solid blue;
  }
  &__table {
    padding: $font_xlarge 0;
    width: 98%;
    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 30%;
        }
        button {
          width: $font_top1;
          border: 1px solid grey;
          background: blue;
          padding: $font_verysmall;
          border-radius: $font_verysmall;
          color: $primary_white;
        }
      }
    }
    .striped-table tbody tr td,
    th {
      background-color: transparent;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #f5f5f5;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #c9c6c6;
    }
    thead{
      position: sticky;
      top: 0;
      z-index: 1;
      background: darkgrey;
    }
  }
  &__popup {
    position: absolute;
    top: 5.9vw;
    width: 100%;
    @include flexJCenter;
    background: $bg_light_grey;
    &__container {
        width: 90%;
    }
  }
}
