@import "../../../styles/variables.scss";
@import "../../../styles/mixins.scss";

.product {
  position: relative;
  width: calc(100% - 18vw);
  .react-tabs {
    width: 95%;
  }
  .react-tabs__tab {
    border: none;
  }

  .react-tabs__tab-list {
    border: none !important;
    display: flex !important;
    justify-content: space-around !important;
    color: #bfc1c2 !important;
    box-shadow: 0 0 1px 2px #0000000c;
    font-size: $font_xsmall !important;
  }
  .react-tabs__tab--selected {
    border: none;
    border-bottom: 2px solid #0e419d;
    color: black !important;
    font-size: $font_small !important;
    background: none;
    font-weight: 0;
  }
  .react-tabs__tab:active {
    border-bottom: 2px #0e419d !important;
    border-top: none;
    border-left: none;
    border-right: none;
    background: none !important;
  }
  .react-tabs__tab:focus:after {
    background: none !important;
  }
  .Cbody {
    width: 100% !important;
    margin-top: 1vw !important;
  }

  .approve-body{
    width: 98% !important;
    margin-top: 1vw !important;
  }

  .MuiPaper-root {
    padding: 1vw;
    background-color: #d2e3fc;
  }

  &__container {
    @include flexColumnACenter;
    background: $bg_light_grey;
    height: 100%;
    overflow-y: scroll;
  }
  &__header {
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 999;
    padding: $font_xverysmall $font_xverysmall;
    @include flexColumnACenter;
    background: $primary_white;
    margin-bottom: 1vw;
    &__section {
      width: 100%;
      display: flex;
      justify-content: space-between;

      &__main {
        padding: 0 0 0.3vw 0;
        @include flexbetween;
        flex-direction: column;
        h4 {
          font-size: $font_button;
        }
        h5 {
          font-size: $font_verysmall;
          color: $font_grey;
          font-weight: 400;
        }
      }
      &__bottom {
        width: 14vw;
      }
    }
    border-bottom: 1px solid #0e419d;
  }
  &__table {
    padding: $font_regular 0;
    width: 100%;

    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 25%;
        }
        button {
          width: auto;
          border: 1px solid grey;
          background: #0e419d;
          padding: 0.6vw 2vw;
          border-radius: $font_xsmall;
          color: $primary_white;
        }
      }
    }

    .col-lg-2 {
      margin-bottom: $font_xsmall;
    }
    .striped-table tbody tr td,
    th {
      background-color: transparent;
      font-size: $font_xsmall !important;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #ffff;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #d2e3fc;
    }
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background: #8ab4f8;
    }
  }
  .col-lg-6 {
    margin-bottom: 0.5vw !important ;
  }

  .form-label {
    font-size: 0.9vw;
    margin-bottom: 0 !important;
  }

  &__popup {
    position: absolute;
    top: 5.9vw;
    width: 100%;
    @include flexJCenter;
    background: $bg_light_grey;
    &__container {
      width: 90%;
    }
  }
}

.indent-product {
  &__table {
    padding: $font_regular 0;
    width: 98%;

    &__header {
      padding: 0 0 $font_xlarge 0;
      &__section {
        @include flexbetween;
        input {
          width: 25%;
        }
        button {
          width: auto;
          border: 1px solid grey;
          background: #0e419d;
          padding: 0.6vw 2vw;
          border-radius: $font_xsmall;
          color: $primary_white;
        }
      }
    }

    .col-lg-2 {
      margin-bottom: $font_xsmall;
    }
    .striped-table tbody tr td,
    th {
      background-color: transparent;
      font-size: $font_xsmall !important;
    }
    .striped-table tbody tr:nth-child(odd) {
      background-color: #ffff;
    }
    .striped-table tbody tr:nth-child(even) {
      background-color: #d2e3fc;
    }
    thead {
      position: sticky;
      top: 0;
      z-index: 1;
      background: #8ab4f8;
    }
  }
}
